	export default {
		data() {
			return {
				ID:0,
        detailInfo:""
			}
		},
		methods: {
			fetchData(){
         var url= "notice/detail";
         var data= {
           'id':this.ID
         }
        this.$Request.get(url,data).then(res => {
         if(res && res.code == 200 && res.data.length !== 0){
           console.log(res.data);
           this.detailInfo = res.data;
          }
        })
      },
      errLoad(e){
        console.log(e);
      }
		},
    onLoad(options) {
     this.ID = options.id;
     this.fetchData();
    }
	}
